<template>
    <div>
      <div class="header-container">
        <h2>{{ yearName }}</h2>
        <div class="button-container">
          <button class="custom-button" type="button" @click="addScore">추가</button>
          <button class="custom-button" type="button" @click="removeScore">삭제</button>
          <button class="custom-button" type="button" @click="saveScore">저장</button>
        </div>
    </div>
      <div>
        <table>
        <thead>
          <tr>
            <th v-for="header in headers" :key="header">
              {{ header }}
            </th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(pd, index) in yearData" :key="index" :class="{selected: pd.isSelected}">
            <td>
              <input type="checkbox" v-model="pd.isSelected">
            </td>
            <td>
              <select v-model="pd.esu">
                <option value="교양">교양</option>
                <option value="전공">전공</option>
              </select>
              
            </td>
            <td>
              <select v-model="pd.isEss">
                <option value="선택">선택</option>
                <option value="필수">필수</option>
              </select>
              
            </td>
            <td @click="editData(pd, 'subject')" v-if="!pd.isEditing">{{ pd.subject }}</td>
            <td v-else><input type="text" v-model="pd.subject" style="text-align: left;"></td>
            <td>
              <select v-model="pd.hakjum">
                <option value="1">1</option>
                <option value="2">2</option>
                <option value="3">3</option>
              </select>
            </td>
            <td @click="editData(pd, 'attendance')" v-if="!pd.isEditing">{{ pd.attendance }}</td>
            <td v-else><input type="number" v-model="pd.attendance" @input="valNumber20(pd, 'attendance')"></td>
            <td @click="editData(pd, 'assignment')" v-if="!pd.isEditing">{{ pd.assignment }}</td>
            <td v-else><input type="number" v-model="pd.assignment" @input="valNumber20(pd, 'assignment')"></td>
            <td @click="editData(pd, 'mid')" v-if="!pd.isEditing">{{ pd.mid }}</td>
            <td v-else><input type="number" v-model="pd.mid" @input="valNumber30(pd, 'mid')"></td>
            <td @click="editData(pd, 'final')" v-if="!pd.isEditing">{{ pd.final }}</td>
            <td v-else><input type="number" v-model="pd.final" @input="valNumber30(pd, 'final')"></td>
            <td>{{ pd.total }}</td>
            <td>{{  }}</td>
            <!-- <td>{{ pd.score }}</td> -->
            <td>
                <select v-if="pd.hakjum === '1'" v-model="pd.grade">
                    <option value="P">P</option>
                    <option value="NP">NP</option>
                </select>
                <span v-else>
                    <span v-if="pd.score" :class="{ 'red-text': pd.score === 'F' }">{{ pd.score }}</span>
                    <span v-else>&nbsp;</span>
                </span>
            </td>
          </tr>
        </tbody>
        <tfoot>
          <tr>
            <th></th>
            <th colspan="3">합계</th>
            <th>{{ totalData[0] }}</th>
            <th>{{ totalData[1] }}</th>
            <th>{{ totalData[2] }}</th>
            <th>{{ totalData[3] }}</th>
            <th>{{ totalData[4] }}</th>
            <th>{{ totalData[5] }}</th>
            <th>{{ totalData[6] }}</th>
            <th>{{ totalData[7] }}</th>
          </tr>
        </tfoot>
      </table>
    </div>
    </div>
</template>
  
<script>
  export default {
    props: {
      yearName: String, // 학년 이름을 받아올 prop
      yearData: Array, // 학년 데이터를 받아올 prop
      headers: Array, // 테이블 헤더를 받아올 prop
      totalData: Array,// 합계 데이터'
    },
    methods: {
        addScore: function(){
            this.$emit('add-score');
        },
        removeScore() {
            this.$emit('remove-score');
        },
        saveScore() {
            this.$emit('save-score');
        },
        editData: function (pd, field) {
            // 수정할 수 있도록 isEditing을 true로 변경
            pd.isEditing = true; 
            pd.editField = field;
        },
        valNumber20: function (pd, field) {
            // 숫자가 아닌 경우
            if (isNaN(pd[field])) {
                alert('숫자를 입력하세요.');
                pd[field] = ''; // 입력값 초기화
                return;
            }
            // 0 미만인 경우
            if (pd[field] < 0) {
                pd[field] = 0;
                alert('0 미만의 값을 입력할 수 없습니다.');
            }
            // 20을 초과하는 경우
            else if (pd[field] > 20) {
                pd[field] = 20;
                alert('20을 초과하는 값을 입력할 수 없습니다.');
            }
        },
        valNumber30: function (pd, field) {
            // 숫자가 아닌 경우
            if (isNaN(pd[field])) {
                alert('숫자를 입력하세요.');
                pd[field] = ''; // 입력값 초기화
                return;
            }
            // 0 미만인 경우
            if (pd[field] < 0) {
                pd[field] = 0;
                alert('0 미만의 값을 입력할 수 없습니다.');
            }
            // 30을 초과하는 경우
            else if (pd[field] > 30) {
                pd[field] = 30;
                alert('30을 초과하는 값을 입력할 수 없습니다.');
            }
        },
    },
  };
</script>
  
