<template>
  <h1>Front-end 과제</h1>
  <div id="app">
    <GradeComponent yearName="1학년" :yearData="dataForYear1" :headers="tableHeaders" :totalData="totalYear1"
    @add-score="this.handleAddScore(this.dataForYear1);" @remove-score="handleRemoveScore1" @save-score="handleSaveScore1"/>
    <GradeComponent yearName="2학년" :yearData="dataForYear2" :headers="tableHeaders" :totalData="totalYear2"
    @add-score="this.handleAddScore(this.dataForYear2)" @remove-score="handleRemoveScore2" @save-score="handleSaveScore2"/>
    <GradeComponent yearName="3학년" :yearData="dataForYear3" :headers="tableHeaders" :totalData="totalYear3"
    @add-score="this.handleAddScore(this.dataForYear3)" @remove-score="handleRemoveScore3" @save-score="handleSaveScore3"/>
  </div>
</template>

<script>
import GradeComponent from './components/GradeComponent.vue';

export default {
  name: 'App',
  data () {
    return {
      tableHeaders : [' ', '이수', '필수', '과목명', '학점', '출석점수', '과제점수', '중간고사', '기말고사', '총점', '평균', '성적'],
      dataForYear1 : [{id: 0, esu:'',isEss:'', subject:'', hakjum: '', attendance: '', assignment: '', mid: '', final: '', total: '', average: '', score: '', }],
      dataForYear2 :[{id: 0, esu:'',isEss:'', subject:'', hakjum: '', attendance: '', assignment: '', mid: '', final: '', total: '', average: '', score: '', }],
      dataForYear3 : [{id: 0, esu:'',isEss:'', subject:'', hakjum: '', attendance: '', assignment: '', mid: '', final: '', total: '', average: '', score: '', }],
      totalYear1 : Array,
      totalYear2 : Array,
      totalYear3 : Array,
    }
  },
  components: {
    GradeComponent,
  },
  methods: {
    handleAddScore: function(dataArray){
      dataArray.push({
        id: dataArray.length + 1,
        esu: '',
        isEss: '',
        subject: '',
        hakjum: '',
        attendance: '',
        assignment: '',
        mid: '',
        final: '',
        total: '',
        average: '',
        score: '',
        isEditing: false
      });
    },
    handleRemoveScore1: function(){
      //1학년 데이터 삭제
      this.dataForYear1 = this.dataForYear1.filter(dataForYear1 => !dataForYear1.isSelected);
      this.allTotalYear1();
    },
    handleRemoveScore2: function(){
      //2학년 데이터 삭제
      this.dataForYear2 = this.dataForYear2.filter(dataForYear2 => !dataForYear2.isSelected);
      this.allTotalYear2();
    },
    handleRemoveScore3: function(){
      //3학년 데이터 삭제
      this.dataForYear3 = this.dataForYear3.filter(dataForYear3 => !dataForYear3.isSelected);
      this.allTotalYear3();
    },
    handleSaveScore1: function(){
      //저장 버튼 클릭 시 총점 계산
      this.dataForYear1.forEach((pd) => {
        const attendance = Number(pd.attendance) 
        const assignment = Number(pd.assignment)
        const mid = Number(pd.mid)
        const final = Number(pd.final)
        if (this.checkDuplicates() == true){
          this.showTotalScore(pd, attendance, assignment, mid, final);
          this.allTotalYear1();
          this.sortDataYear1();
        }
      });
    },
    handleSaveScore2: function(){
      //저장 버튼 클릭 시 총점 계산
      this.dataForYear2.forEach((pd) => {
        const attendance = Number(pd.attendance)
        const assignment = Number(pd.assignment)
        const mid = Number(pd.mid)
        const final = Number(pd.final)
        if (this.checkDuplicates() == true){
          this.showTotalScore(pd, attendance, assignment, mid, final);
          this.allTotalYear2();
          this.sortDataYear2();
        }
      });
    },
    handleSaveScore3: function(){
      //저장 버튼 클릭 시 총점 계산
      this.dataForYear3.forEach((pd) => {
        const attendance = Number(pd.attendance)
        const assignment = Number(pd.assignment)
        const mid = Number(pd.mid)
        const final = Number(pd.final)
        if (this.checkDuplicates() == true){
          this.showTotalScore(pd, attendance, assignment, mid, final);
          this.allTotalYear3();
          this.sortDataYear3();
        }
      });
    },
    calculateGrade(total) {
      //총점을 기반으로 성적 계산
      if (total >= 95) {
        return 'A+';
      } else if (total >= 90) {
        return 'A0';
      } else if (total >= 85) {
        return 'B+';
      } else if (total >= 80) {
        return 'B0';
      } else if (total >= 75) {
        return 'C+';
      } else if (total >= 70) {
        return 'C0';
      } else if (total >= 65) {
        return 'D+';
      } else if (total >= 60) {
        return 'D0';
      } else {
        return 'F';
      }
    },
    showTotalScore: function(pd, att, ass, mid, final){
      //총점 및 성적 표시
      if(att == "" || ass == "" || mid == "" || final == ""){
          pd.total = '';
          pd.score = '';
        }
        else{
          pd.total = att + ass + mid + final;
          pd.score = this.calculateGrade(pd.total);
        }
    },
    getTotalDataCount1() {
      //총점이 입력된 행만 카운트 (1학년)
      return this.dataForYear1.filter((pd) => pd.total !== '').length;
    },
    getTotalDataCount2() {
      //총점이 입력된 행만 카운트 (2학년)
      return this.dataForYear2.filter((pd) => pd.total !== '').length;
    },
    getTotalDataCount3() {
      //총점이 입력된 행만 카운트 (3학년)
      return this.dataForYear3.filter((pd) => pd.total !== '').length;
    },
    calculateColumnTotalYear1(columnName) {
      // columnName 열에 대한 합계 계산
      return this.dataForYear1.reduce((total, pd) => total + parseInt(pd[columnName] || 0), 0);
    },
    calculateColumnTotalYear2(columnName) {
      // columnName 열에 대한 합계 계산
      return this.dataForYear2.reduce((total, pd) => total + parseInt(pd[columnName] || 0), 0);
    },
    calculateColumnTotalYear3(columnName) {
      // columnName 열에 대한 합계 계산
      return this.dataForYear3.reduce((total, pd) => total + parseInt(pd[columnName] || 0), 0);
    },
    allTotalYear1: function(){
      //1학년 총점 계산
      const hakjumTmp = this.calculateColumnTotalYear1('hakjum')
      const attTmp = this.calculateColumnTotalYear1('attendance')
      const assTmp = this.calculateColumnTotalYear1('assignment')
      const midTmp = this.calculateColumnTotalYear1('mid')
      const finalTmp = this.calculateColumnTotalYear1('final')
      const totalTmp = this.calculateColumnTotalYear1('total')
      const tmp = this.getTotalDataCount1()
      this.totalYear1 = [hakjumTmp, attTmp, assTmp, midTmp, finalTmp, totalTmp, (totalTmp/tmp).toFixed(2), this.calculateGrade(totalTmp/tmp)]
    },
    allTotalYear2: function(){
      //2학년 총점 계산
      const hakjumTmp = this.calculateColumnTotalYear2('hakjum')
      const attTmp = this.calculateColumnTotalYear2('attendance')
      const assTmp = this.calculateColumnTotalYear2('assignment')
      const midTmp = this.calculateColumnTotalYear2('mid')
      const finalTmp = this.calculateColumnTotalYear2('final')
      const totalTmp = this.calculateColumnTotalYear2('total')
      const tmp = this.getTotalDataCount2()
      this.totalYear2 = [hakjumTmp, attTmp, assTmp, midTmp, finalTmp, totalTmp, (totalTmp/tmp).toFixed(2), this.calculateGrade(totalTmp/tmp)]
    },
    allTotalYear3: function(){
      //3학년 총점 계산
      const hakjumTmp = this.calculateColumnTotalYear3('hakjum')
      const attTmp = this.calculateColumnTotalYear3('attendance')
      const assTmp = this.calculateColumnTotalYear3('assignment')
      const midTmp = this.calculateColumnTotalYear3('mid')
      const finalTmp = this.calculateColumnTotalYear3('final')
      const totalTmp = this.calculateColumnTotalYear3('total')
      const tmp = this.getTotalDataCount3()
      this.totalYear3 = [hakjumTmp, attTmp, assTmp, midTmp, finalTmp, totalTmp, (totalTmp/tmp).toFixed(2), this.calculateGrade(totalTmp/tmp)]
    },
    checkDuplicates() {

      let tmp = true;

      const combinedData = [
      ...this.dataForYear1,
      ...this.dataForYear2,
      ...this.dataForYear3
      ];
      const subjectSet = new Set();
      const filteredData = [];

      for (const item of combinedData) {
        const subject = item.subject.trim();
        
        if (subject === '') {
          continue;
        }
        if (subjectSet.has(subject)) {
          alert('중복된 과목명이 있습니다: ' + subject);
          tmp = false;
          continue;
        }
        // 중복이 아니면 subjectSet에 추가하고 filteredData에 추가
        subjectSet.add(subject);
        filteredData.push(item);
      }
      this.filteredCombinedData = filteredData;
      return tmp;
    },
    sortDataYear1(){
      this.dataForYear1.sort((a, b) => {
      // esu 열 비교
      const esuA = a.esu.toLowerCase();
      const esuB = b.esu.toLowerCase();
      if (esuA < esuB) {
        return -1;
      }
      if (esuA > esuB) {
        return 1;
      }
      // esu가 같은 경우 isEss 열 비교
      const isEssA = a.isEss.toLowerCase();
      const isEssB = b.isEss.toLowerCase();
      if (isEssA < isEssB) {
        return -1;
      }
      if (isEssA > isEssB) {
        return 1;
      }
      // isEss가 같은 경우 subject 열 비교
      const subjectA = a.subject.toLowerCase();
      const subjectB = b.subject.toLowerCase();
      if (subjectA < subjectB) {
        return -1;
      }
      if (subjectA > subjectB) {
        return 1;
      }
      return 0;
    });
    },
    sortDataYear2(){
      this.dataForYear2.sort((a, b) => {
      // esu 열 비교
      const esuA = a.esu.toLowerCase();
      const esuB = b.esu.toLowerCase();
      if (esuA < esuB) {
        return -1;
      }
      if (esuA > esuB) {
        return 1;
      }
      // esu가 같은 경우 isEss 열 비교
      const isEssA = a.isEss.toLowerCase();
      const isEssB = b.isEss.toLowerCase();
      if (isEssA < isEssB) {
        return -1;
      }
      if (isEssA > isEssB) {
        return 1;
      }
      // isEss가 같은 경우 subject 열 비교
      const subjectA = a.subject.toLowerCase();
      const subjectB = b.subject.toLowerCase();
      if (subjectA < subjectB) {
        return -1;
      }
      if (subjectA > subjectB) {
        return 1;
      }
      return 0;
    });
    },
    sortDataYear3(){
      this.dataForYear3.sort((a, b) => {
      // esu 열 비교
      const esuA = a.esu.toLowerCase();
      const esuB = b.esu.toLowerCase();
      if (esuA < esuB) {
        return -1;
      }
      if (esuA > esuB) {
        return 1;
      }
      // esu가 같은 경우 isEss 열 비교
      const isEssA = a.isEss.toLowerCase();
      const isEssB = b.isEss.toLowerCase();
      if (isEssA < isEssB) {
        return -1;
      }
      if (isEssA > isEssB) {
        return 1;
      }
      // isEss가 같은 경우 subject 열 비교
      const subjectA = a.subject.toLowerCase();
      const subjectB = b.subject.toLowerCase();
      if (subjectA < subjectB) {
        return -1;
      }
      if (subjectA > subjectB) {
        return 1;
      }
      return 0;
    });
    },
  },
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 30px;
}
</style>
